import * as mutations from './mutation-types';

export default {
  /* eslint-disable no-param-reassign */
  [mutations.SET_CONTENTS](state, contents) {
    state.contents = contents;
  },
  /* eslint-disable no-param-reassign */
  [mutations.SET_PAGE_NAME](state, pageName) {
    state.pageName = pageName;
  },
};



