import Vuex from 'vuex';
import Vue from 'vue';

import state from './state';

import Homepage from './modules/HomePage/index';
import Dashboard from './modules/Dashboard/index';
import Career from './modules/Career/index';
import Framework from './modules/Framework/index';
import Projects from './modules/Projects/index';
import Contacts from './modules/Contacts/index';

Vue.use(Vuex);

/* eslint-disable no-new */
const store = new Vuex.Store({
  modules: {
    Homepage,
    Dashboard,
    Career,
    Framework,
    Projects,
    Contacts,
  },
  state,
});

export default store;



