import * as mutations from './mutation-types';
import getPage from '../../../api';

export const HomepageSetPageName = ({ commit }, pageName) => {
  commit(mutations.SET_PAGE_NAME, pageName);
};

export const HomepageSetContents = ({ commit }) => new Promise(async (resolve) => {
  const contents = await getPage({ pageName: 'Homepage' });
  commit(mutations.SET_CONTENTS, contents);
  resolve();
});



