import * as mutations from './mutation-types';
import getPage from '../../../api';

export const FrameworkSetPageName = ({ commit }, pageName) => {
  commit(mutations.SET_PAGE_NAME, pageName);
};

export const FrameworkSetContents = ({ commit }) => new Promise(async (resolve) => {
  const contents = await getPage({ pageName: 'Framework' });
  commit(mutations.SET_CONTENTS, contents);
  resolve();
});



