import { mapState, mapActions, mapGetters } from 'vuex';

import Icon from 'vue-awesome/components/Icon.vue';
import 'vue-awesome/icons/times';

export default {
  props: ['isHidden', 'isFocused'],
  data(){
    return {
    }
  },
  components: {
    Icon
  },
  methods: {
    selectPage(index){
      this.$emit('selectPage', index);
    },
    hoverPageOn(index){
      this.$emit('hoverPageOn', index);
    },
    hoverPageOff(index){
      this.$emit('hoverPageOff', index);
    },
  },
  computed: {
    ...mapGetters([
      'DashboardIsOnTop',
      'DashboardIsBorderVisible',
      'DashboardContents',
    ]),
  }
}



