import { mapGetters } from 'vuex';

import Icon from 'vue-awesome/components/Icon.vue';
import 'vue-awesome/icons/fa-arrow-down';
import 'vue-awesome/icons/external-link';

export default {
  props: ['backToMenu'],
  components: {
    Icon
  },
  beforeRouteLeave(to, from, next){
    this.backToMenu();
    return next();
  },
  computed: {
    ...mapGetters([
      'ProjectsContents'
    ]),
  }
};



