import * as mutations from './mutation-types';
import getPage from '../../../api';

export const CareerSetPageName = ({ commit }, pageName) => {
  commit(mutations.SET_PAGE_NAME, pageName);
};

export const CareerSetContents = ({ commit }) => new Promise(async (resolve) => {
  const contents = await getPage({ pageName: 'Career' });
  commit(mutations.SET_CONTENTS, contents);
  resolve();
});



