import * as mutations from './mutation-types';

export default {
  /* eslint-disable no-param-reassign */
  [mutations.SET_CONTENTS](state, contents) {
    state.contents = contents;
  },
  /* eslint-disable no-param-reassign */
  [mutations.SET_PAGE_NAME](state, pageName) {
    state.pageName = pageName;
  },
  /* eslint-disable no-param-reassign */
  [mutations.SET_SELECTED_ELEMENT](state, selectedElement) {
    state.selectedElement = selectedElement;
  },
  /* eslint-disable no-param-reassign */
  [mutations.SET_IS_ON_TOP](state, value) {
    state.isOnTop = value;
  },
  /* eslint-disable no-param-reassign */
  [mutations.SET_SELECTED_INDEX](state, selectedElementIndex) {
    state.selectedElementIndex = selectedElementIndex;
  },
  /* eslint-disable no-param-reassign */
  [mutations.SET_USABLE](state, value) {
    state.usable = value;
  },
  /* eslint-disable no-param-reassign */
  [mutations.SET_BORDER_VISIBILITY](state, value) {
    state.showBorder = value;
  },
};



