import * as mutations from './mutation-types';
import getPage from '../../../api';

export const ContactsSetPageName = ({ commit }, pageName) => {
  commit(mutations.SET_PAGE_NAME, pageName);
};

export const ContactsSetContents = ({ commit }) => new Promise(async (resolve) => {
  const contents = await getPage({ pageName: 'Contacts' });
  commit(mutations.SET_CONTENTS, contents);
  resolve();
});



