import VueRouter from 'vue-router';
import Vue from 'vue';

import HomePage from '../pages/HomePage/HomePage.vue';
import Dashboard from '../pages/Dashboard/Dashboard.vue';
import Career from '../pages/Career/Career.vue';
import Framework from '../pages/Framework/Framework.vue';
import Projects from '../pages/Projects/Projects.vue';
import Contacts from '../pages/Contacts/Contacts.vue';

import store from '../store';
import * as guards from './guards';

Vue.use(VueRouter);

const routes = [
  { name: 'Homepage', path: '/', component: HomePage, beforeEnter: guards.beforeHomepage(store) },
  { name: 'Dashboard',
    path: '/dashboard',
    component: Dashboard,
    beforeEnter: guards.beforeDashboard(store),
    children: [
      {
        path: 'career-and-education',
        component: Career,
        beforeEnter: guards.beforeCareer(store),
      },
      {
        path: 'languages-and-framework',
        component: Framework,
        beforeEnter: guards.beforeFramework(store),
      },
      {
        path: 'works-and-projects',
        component: Projects,
        beforeEnter: guards.beforeProjects(store),
      },
      {
        path: 'contacts-and-links',
        component: Contacts,
       beforeEnter: guards.beforeContacts(store),
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;



