import * as mutations from './mutation-types';
import getPage from '../../../api';

export const DashboardSetPageName = ({ commit }, pageName) => {
  commit(mutations.SET_PAGE_NAME, pageName);
};

export const DashboardSetContents = ({ commit }) => new Promise(async (resolve) => {
  const contents = await getPage({ pageName: 'Dashboard' });
  commit(mutations.SET_CONTENTS, contents);
  resolve();
});

export const DashboardSetIsOnTop = ({ commit }, value) => {
  commit(mutations.SET_IS_ON_TOP, value);
};

export const DashboardSetSelectedElement = ({ commit }, selectedElement) => {
  commit(mutations.SET_SELECTED_ELEMENT, selectedElement);
};

export const DashboardSetSelectedElementIndex = ({ commit }, selectedElementIndex) => {
  commit(mutations.SET_SELECTED_INDEX, selectedElementIndex);
};

export const DashboardSetUsable = ({ commit }, value) => {
  commit(mutations.SET_USABLE, value);
};

export const DashboardSetBorderVisibility = ({ commit }, value) => {
  commit(mutations.SET_BORDER_VISIBILITY, value);
};



