import * as mutations from './mutation-types';
import getPage from '../../../api';

export const ProjectsSetPageName = ({ commit }, pageName) => {
  commit(mutations.SET_PAGE_NAME, pageName);
};

export const ProjectsSetContents = ({ commit }) => new Promise(async (resolve) => {
  const contents = await getPage({ pageName: 'Projects' });
  commit(mutations.SET_CONTENTS, contents);
  resolve();
});



