import { mapState, mapActions, mapGetters } from 'vuex';

import { TweenMax } from 'gsap';

import Navbar from '../../components/Navbar/Navbar.vue';
import Mainmenu from '../../components/Mainmenu/Mainmenu.vue';

export default {
  components: {
    myNav: Navbar,
    Mainmenu
  },
  data(){
    return {
      elements: [],
      rotationGuard: false
    }
  },
  mounted(){
    const domElements = document.getElementsByClassName('mainmenu__preview');
    this.elements = Array.prototype.slice.call(domElements);
    TweenMax.staggerTo(this.elements, 1, {
      delay: 2,
      opacity: 1,
      ease: Linear.easeIn
    }, 0.5);
  },
  methods: {
    setRotationGuard(value) {
      this.rotationGuard = value;
    },
    isHidden(index) {
      if(this.DashboardSelectedIndex === -1)
        return false;
      return (index !== this.DashboardSelectedIndex);
    },
    isFocused(index) {
      return (index === this.DashboardSelectedIndex)
    },
    menuOpened(){
      this.DashboardSetIsOnTop(true);
      this.DashboardSetUsable(true);
      this.$router.push(`/dashboard/${this.DashboardActiveSubPage}`);
    },
    menuClosed(){
      this.DashboardSetIsOnTop(false);
      this.DashboardSetUsable(true);
    },
    hoverPageOn(index) {
      if(this.DashboardIsOnTop || !this.DashboardIsUsable)
        return;
      // this.rotationGuard = true;
      const el = this.elements[index];
      const title = el.children[0];
      TweenMax.to(title, 1, {
        delay: .1,
        rotationY: 360,
        clearProps: 'rotationY',
        cursor: 'pointer',
        onComplete: this.setRotationGuard,
        onCompleteParams: [false]
        });
      TweenMax.to(el, .5, {
        scale: 0.8,
      });
    },
    hoverPageOff(index) {
      if(this.DashboardIsOnTop || !this.DashboardIsUsable)
        return;
      const el = this.elements[index];
      const title = el.children[0];
      TweenMax.to(el, .5, {
        scale: 1,
      });
    },
    selectPage(index) {
      if(this.isFocused(index) || !this.DashboardIsUsable)
        return;
      this.DashboardSetUsable(false);
      this.hideAll(index);
    },
    hideAll(index){
      TweenMax.to(this.elements, 2, { opacity: 0, onComplete: this.showSelected, onCompleteParams: [index] });
    },
    showSelected(index){
      this.DashboardSetSelectedElement(this.elements[index]);
      this.DashboardSetSelectedElementIndex(index);
      this.elements[index].style.transform = 'initial';
      TweenMax.to(this.DashboardSelectedElement, 1, { opacity: 1, onComplete: this.moveSelectedToTop });
    },
    moveSelectedToTop(){
      TweenMax.to(this.DashboardSelectedElement, 1, {
        top: '.5rem',
        height: '6rem',
        width: '6rem',
        marginTop: '0',
        marginRight: '-3rem',
        fontSize: '10px',
        onComplete: this.menuOpened,
       });
    },
    backToMenu(){
      this.DashboardSetIsOnTop(false);
      this.DashboardSetBorderVisibility(false);
      this.DashboardSetUsable(false);
      this.moveSelectedToCenter();
    },
    showAll(index){
      this.DashboardSetSelectedElementIndex(-1);
      this.DashboardSelectedElement.style.marginTop = '2rem';
      this.DashboardSelectedElement.style.marginRight = '2rem';
      TweenMax.to(this.elements, 1, {
        opacity: 1,
        clearProps: 'margin-top, margin-right',
        onComplete: this.menuClosed,
        // onCompleteParams: [false, true],
       });
    },
    moveSelectedToCenter(){
      TweenMax.to(this.DashboardSelectedElement, 2, {
        top: '50%',
        height: '12rem',
        width: '12rem',
        marginTop: '-6rem',
        marginRight: '-6rem',
        fontSize: '1rem',
        opacity: 0,
        onComplete: this.showAll
       });
    },
    ...mapActions([
      'DashboardSetIsOnTop',
      'DashboardSetUsable',
      'DashboardSetSelectedElementIndex',
      'DashboardSetSelectedElement',
      'DashboardSetBorderVisibility',
    ]),
  },
  computed: {
    ...mapState([
      'Dashboard',
    ]),
    ...mapGetters([
      'DashboardIsOnTop',
      'DashboardIsUsable',
      'DashboardSelectedElement',
      'DashboardSelectedIndex',
      'DashboardIsBorderVisible',
      'DashboardContents',
      'DashboardActiveSubPage',
    ]),
  }
};



