import { mapGetters } from 'vuex';

import { TweenMax } from 'gsap';

import Icon from 'vue-awesome/components/Icon.vue';
import 'vue-awesome/icons/hand-pointer-o';

const startKnowMore = (knowMore) => {
  // TweenMax.to(knowMore, 1, { delay: 0, opacity: 1, ease: SlowMo.easeIn });
  TweenMax.to(knowMore, 2, { delay: 0, opacity: 1, bottom: '1rem', ease: Back.easeOut });
};

export default {
  name: 'Homepage',
  components: {
    Icon,
  },
  data(){
    return {
      splashPageVisible: true
    }
  },
  mounted(){
    this.initAnimation();
  },
  methods: {
    initAnimation(){
      const { title, about, knowMore } = this.$refs;
      /* eslint-disable no-undef */
      const items = document.getElementsByClassName('list__item');
      TweenMax.to(title, 2, {
        marginTop: '1rem',
        opacity: 1
        });
      TweenMax.to(about, 2, {
        delay: 1,
        marginLeft: '4rem',
        opacity: 1
       });
      /* eslint-disable no-undef */
      TweenMax.staggerTo(items, 1, {
        delay: 2.5,
        opacity: 1,
        ease: Linear.easeIn
       }, 0.8, startKnowMore, [knowMore]);
    },
    knowMoreHoverEnter() {
      const { knowMore } = this.$refs;
      TweenMax.to(knowMore, 0.5, {
        delay: 0,
        color: 'black',
        backgroundColor: '#C7E0E2'
       });
    },
    knowMoreHoverLeave() {
      const { knowMore } = this.$refs;
      TweenMax.to(knowMore, 0.5, {
        delay: 0,
        color: '#C7E0E2',
        backgroundColor: 'rgba(0,0,0,0)'
       });
    },
    goToMainMenu() {
      this.$router.push({ name: 'Dashboard' });
    },
  },
  computed: {
    ...mapGetters([
      'HomepageGetTopContents',
      'HomepageGetMiddleContents',
      'HomepageGetBottomContents',
    ]),
  },
};



