export const DashboardContents = state => state.contents;

export const DashboardIsUsable = state => state.usable;

export const DashboardIsOnTop = state => state.isOnTop;

export const DashboardIsBorderVisible = state => state.showBorder;

export const DashboardSelectedIndex = state => state.selectedElementIndex;

export const DashboardSelectedElement = state => state.selectedElement;

export const DashboardActiveSubPage = (state) => {
  const categories = state.contents.categories;
  return categories[state.selectedElementIndex].path;
};

export const DashboardHashOfCategories = (state) => {
  const categories = state.contents.categories;
  const hashOfCategories = {};
  categories.forEach((category, index) => {
    hashOfCategories[`/dashboard/${category.path}`] = index;
  });
  return hashOfCategories;
};



